<template>
  <div>
    <top-bar></top-bar>
    <section class="container">
      <section class="columns">
        <!--Sidebar -->
        <side-bar></side-bar>
        <a href="#sidebar-close" id="sidebar-close" class="docs-nav-clear"></a>

        <!--Sidebar  -->
        <div class="docs-content column col-10 col-sm-12 centered">
          <section id="compiling" class="container" style="margin-top: 80px">
            <header class="text-center">
              <h3></h3>
            </header>
            <!--Modal:-->
            <a @click="modalSaveActive?modalSaveActive=false:modalSaveActive=true" class="float-right btn btn-primary btn-action btn-lg" style="border-radius: 2em">
              <i class="icon icon-plus"></i>
            </a>
            <table class="table table-striped">
              <thead>
                <tr>
                  <th>
                    <label class="form-checkbox">
                      <input type="checkbox" :disabled="categorys.length==0?true:false" v-model="selectAll" />
                      <i class="form-icon"></i> 
                    </label>
                  </th>
                  <th></th>
                  <th></th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="category in categorys">
                  <td>
                    <label class="form-checkbox">
                      <input type="checkbox" :value="category._id" v-model="checkedOptions" />
                      <i class="form-icon"></i> &nbsp;
                    </label>
                  </td>
                  <td>{{category.name}}</td>
                  <td>{{category.createdAt}}</td>
                  <td>
                    <button @click="updateCategoryOpenModal(category._id,category.name)" class="btn btn-primary btn-action btn-lg">
                      <i class="icon icon-edit"></i>
                    </button>
                    <button @click="deleteCategory(category._id)" class="btn btn-primary btn-action btn-lg">
                      <i class="icon icon-delete"></i>
                    </button>
                  </td>
                </tr>
              </tbody>
            </table>
            <div class="column">
              <span class="float-left">
                <button :disabled="categorys.length==0?true:false" class="btn" @click="deleteCategorys(`${checkedOptions}`)"></button>
              </span>
              <span class="float-right">
                               <!--Paging -->
    <paginate :pageCount="Number(`${count/10}`)" :prevText="''" :nextText="''" :containerClass="'pagination'" :page-class="'page-item'" :prev-class="'page-item'" :next-class="'page-item'" :clickHandler="clickCallback">
    </paginate>     
              </span>
            </div>
          </section>

          <!--Modal:-->
          <section class="container">
            <div class="columns">
              <div class="column">
                <div class="modal" v-bind:class="{active:modalSaveActive}">
                  <a class="modal-overlay" aria-label="Close"></a>
                  <div class="modal-container col-3 col-xs-12" role="document">
                    <div class="modal-header">
                      <a @click="modalSaveActive?modalSaveActive=false:modalUpdateActive=true" class="btn btn-clear float-right" aria-label="Close"></a>
                      <div class="modal-title"></div>
                    </div>
                    <div class="modal-body">
                      <div class="content">
                        <div class="form-horizontal">
                          <div class="form-group">
                            <div class="col-2">
                              <label class="form-label"></label>
                            </div>
                            <div class="col-10">
                              <input class="form-input" type="text" @input="ADD_NEW_CATEGORY({name: $event.target.value})" placeholder="" />
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div class="modal-footer">
                      <button class="btn btn-primary" @click="saveCategory"></button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </section>

          <!--Modal:-->
          <section class="container">
            <div class="columns">
              <div class="column">
                <div class="modal" v-bind:class="{active:modalUpdateActive}">
                  <a class="modal-overlay" aria-label="Close"></a>
                  <div class="modal-container col-3 col-xs-12" role="document">
                    <div class="modal-header">
                      <a @click="modalUpdateActive?modalUpdateActive=false:modalUpdateActive=true" class="btn btn-clear float-right" aria-label="Close"></a>
                      <div class="modal-title"></div>
                    </div>
                    <div class="modal-body">
                      <div class="content">
                        <div class="form-horizontal">
                          <div class="form-group">
                            <div class="col-2">
                              <label class="form-label"></label>
                            </div>
                            <div class="col-10">
                              <input class="form-input" :value="updateCurrentName" type="text" @input="ADD_NEW_CATEGORY({name: $event.target.value})" placeholder="" />
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div class="modal-footer">
                      <button class="btn btn-primary" :value="updateCurrentId" @click="updateCategory($event.target.value)"></button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </section>

        </div>
        <!--Sidebar  -->
      </section>
    </section>
  </div>
</template>

<script>
import TopBar from './../components/TopBar'
import SideBar from './../components/SideBar'

import Vue from 'vue'
import Paginate from 'vuejs-paginate'
Vue.component('paginate', Paginate)

// Vue.js 
// htmlvalue,disabled :value="{{}}" :disabled="true"
// class v-bind:class=""  style  v-bind:style="{color:red}"

import { mapState, mapMutations, mapActions } from 'vuex'
export default {
  data() {
    return {
      modalUpdateActive: false,
      modalSaveActive: false,
      checkedOptions: [],
      updateCurrentId: '',
      updateCurrentName: ''
    }
  },
  components: {
    TopBar,
    SideBar
  },
  created() {
    this.getCategorys(1)
  },
  computed: {
    ...mapState(['categorys', 'category', 'count']),
    // getter,setter
    selectAll: {
      get: function() {
        // categorys=0,false
        if (this.categorys.length === 0) {
          return false
        } else return this.checkedOptions.length === this.categorys.length
      },
      set: function(value) {
        let checkedOptions = []
        // value:true
        if (value) {
          // categorysVuex store
          // [ input v-model // :value='']
          this.categorys.forEach(function(i) {
            checkedOptions.push(i._id)
          })
        }
        // 
        this.checkedOptions = checkedOptions
      }
    }
  },
  methods: {
    ...mapActions(['getCategorys', 'deleteCategory', 'saveCategory', 'updateCategory', 'deleteCategorys']),
    ...mapMutations(['ADD_NEW_CATEGORY', 'SET_CATEGORYS', 'SET_COUNT']),
    // 
    updateCategoryOpenModal(id, name) {
      this.modalUpdateActive = true
      // id,name
      this.updateCurrentId = id
      this.updateCurrentName = name
    },
    clickCallback: function(page) {
      // 
      this.getCategorys(page)
    }
  }
}  
</script>



