import Vue from 'vue'
import actions from './actions'
import mutations  from './mutations'

// Vuex
import Vuex from 'vuex'

// Vuex
//  Vue.use(Vuex)
Vue.use(Vuex);
//  Vuex  store"store" (state)
// Vuex  Vue  store  store 
// store
const store = new Vuex.Store({
  state: {
    isLoading: false,
    nickName: '',
    role: '',
    token: '',
    privileges: '',
    meta: {  //json
      // description: '',
      // keywords: '',
      // headerscript: '',
      // footerscript: '',
      // themecss: '',
    },
    info: {}, //
    categorys: [], // 
    categorysOptions: [],// SelectOptions
    category: {},
    articles: [], // 
    article: {},
    attachments: [], // 
    contacts:[], // 
    banners: [], // Banner
    banner: {},
    postsOptions: [],// SelectOptions,
    groups: [], // Group
    group: {},
    accounts: [], // 
    account: {},
    groupsOptions: [],// SelectOptions,
    count: 0,
    googleAnalytics: {}
  },
  //  store 
  //  store (commit) mutations
  // 
  mutations,
  actions
})

export default store


