<template>
  <div>
    <top-bar></top-bar>
    <section class="container">
      <section class="columns">
        <!--Sidebar -->
        <side-bar></side-bar>
        <a href="#sidebar-close" id="sidebar-close" class="docs-nav-clear"></a>

        <!--Sidebar  -->
        <div class="docs-content column col-10 col-sm-12 centered">
          <section id="compiling" class="container" style="margin-top: 80px">
            <header class="text-center">
              <h3></h3>
            </header>
            <!-- v1.0.1 Modal:CSV-->
            <!-- <a @click="modalSaveActive?modalSaveActive=false:modalSaveActive=true" class="float-right btn btn-primary btn-action btn-lg" style="border-radius: 2em">
                  <i class="icon icon-download"></i>
              </a> -->
            <table class="table table-striped">
              <thead>
                <tr>
                  <th>
                    <label class="form-checkbox">
                      <input type="checkbox" :disabled="contacts.length==0?true:false" v-model="selectAll" />
                      <i class="form-icon"></i> 
                    </label>
                  </th>
                  <th></th>
                  <th>E-mail</th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="contact in contacts">
                  <td>
                    <label class="form-checkbox">
                      <input type="checkbox" :value="contact._id" v-model="checkedOptions" />
                      <i class="form-icon"></i> &nbsp;
                    </label>
                  </td>
                  <td>{{contact.realName}}</td>
                  <td>{{contact.email}}</td>
                  <td>{{contact.mobile}}</td>
                  <td>{{contact.address}}</td>
                  <td>
                    <textarea class="form-input" :value="contact.detail" rows="1"></textarea>
                  </td>
                  <td>{{contact.createdAt}}</td>
                  <td>
                    <button @click="deleteContactForm(contact._id)" class="btn btn-primary btn-action btn-lg">
                      <i class="icon icon-delete"></i>
                    </button>
                  </td>
                </tr>
              </tbody>
            </table>
            <div class="column">
              <span class="float-left">
                <button :disabled="contacts.length==0?true:false" class="btn" @click="deleteContactForms(`${checkedOptions}`)"></button>
              </span>
              <span class="float-right">
                         <!--Paging -->
    <paginate :pageCount="Number(`${count/10}`)" :prevText="''" :nextText="''" :containerClass="'pagination'" :page-class="'page-item'" :prev-class="'page-item'" :next-class="'page-item'" :clickHandler="clickCallback">
    </paginate>
              </span>
            </div>
          </section>

        </div>
        <!--Sidebar  -->
      </section>
    </section>
  </div>
</template>

<script>
import TopBar from './../components/TopBar'
import SideBar from './../components/SideBar'

// Vue.js 
// htmlvalue,disabled :value="{{}}" :disabled="true"
// class v-bind:class=""  style  v-bind:style="{color:red}"

import { mapState, mapMutations, mapActions } from 'vuex'
export default {
  data() {
    return {
      checkedOptions: []
    }
  },
  components: {
    TopBar,
    SideBar
  },
  created() {
    this.getContactForms(1)
  },
  computed: {
    ...mapState(['contacts', 'count']),
    // getter,setter
    selectAll: {
      get: function() {
        // contacts=0,false
        if (this.contacts.length === 0) {
          return false
        } else return this.checkedOptions.length === this.contacts.length
      },
      set: function(value) {
        let checkedOptions = []
        // value:true
        if (value) {
          // contactsVuex store
          // [ input v-model // :value='']
          this.contacts.forEach(function(i) {
            checkedOptions.push(i._id)
          })
        }
        // 
        this.checkedOptions = checkedOptions
      }
    }
  },
  methods: {
    ...mapActions(['getContactForms', 'deleteContactForm', 'deleteContactForms']),
    ...mapMutations(['SET_CONTACT_FORMS', 'SET_COUNT']),
    clickCallback: function(page) {
      // 
      this.getContactForms(page)
    }
  }
}  
</script>



