<template>
    <div>
        <section class="section section-header bg-header">
            <section class="grid-header container grid-960">
                <nav class="navbar">
                    <section class="navbar-section">
                        <a href="index.html" class="navbar-brand mr-10">4MCloudServer</a>
                    </section>
                    <section class="navbar-section">
                        <a href="/" class="btn btn-primary"></a>
                    </section>
                </nav>
            </section>
        </section>

        <section class="section-login">
            <div class="container">
                <div class="columns">
                    <div class="column col-3 col-xs-10 centered">
                        <header class="text-center">
                            <h3> </h3>
                        </header>
                        <div class="form-group">
                            <input class="form-input" v-model="email" autocomplete="off" type="text" placeholder="">
                        </div>
                        <div class="form-group">
                            <input class="form-input" v-model="password" autocomplete="off" type="password" placeholder="">
                            <p class="form-input-hint">{{errInfo}}</p>
                        </div>
                        <div class="form-group">
                            <button class="btn btn-primary btn-block" @click="login"> </button>
                        </div>

                    </div>
                </div>
            </div>
        </section>

        <footer class="section-footer bg-gray">
            <section id="copyright" class="grid-footer container grid-960">
                <p>
                    <a href="#" target="_blank">4MWorkOrder</a> |
                    <a href="#" target="_blank">GitHub Repo</a> |
                    <a href="#" target="_blank">Price</a> | Version
                    <span class="version"></span>
                </p>
                <p>Designed and built with  by
                    <a href="https://www.4-m.cn" target="_blank">4mdevstudio</a>. Licensed under the
                    <a href="#" target="_blank">MIT License</a>.</p>
            </section>
        </footer>
    </div>
</template>

<script>
import './../assets/css/spectre.min.css'
import './../assets/css/spectre-exp.min.css'
import './../assets/css/spectre-icons.min.css'

//  Vue  Vuex 
import axios from 'axios'
//  mapState 
import { mapState } from 'vuex'
export default {
    data() {
        return {
            email: '',
            password: '',
            errInfo: ''
        }
    },
    // mapState  computed 
    computed: {
        //...mapState()
        // state  mapState 
        //mapState(['count'])  this.count  store.state.count
        ...mapState(['nickName', 'role', 'token', 'privileges'])
    },
    mounted() {
        //Vuemounted
        //checkLogin()
        this.checkLogin()
    },
    methods: {
        checkLogin() {
            //VuexStoretoken,token
            //token,
            let token = window.localStorage.getItem('X-4MDEVSTUDIO-TOKEN')
            axios.get('/user/checkLogin', {
                headers: {
                    'X-MC-TOKEN': token
                }
            }).then((res) => {
                if (!res.data.err) {
                    //                    
                    window.location.href = '/'
                }
            })
        },
        login() {
            if (!this.email.length) {
                this.errInfo = ''
                return
            }
            if (!this.password.length) {
                this.errInfo = ''
                return
            }
            axios.post('/user/session', {
                email: this.email,
                password: this.password
            }).then((res) => {
                if (!res.data.err) {
                    //  store 
                    // console.log("1"+this.$store.state.nickName)  
                    //  store (commit) mutations
                    this.$store.commit('updateUserInfo', {
                        nickName: res.data.data.info.realName,
                        role: res.data.data.info.role,
                        token: res.data.data.token,
                        privileges: res.data.data.info.privileges
                    })
                    // token,state
                    window.localStorage.setItem('X-4MDEVSTUDIO-TOKEN', this.$store.state.token)
                    window.location.href = '/'
                } else {
                    this.errInfo = ''
                }
            })
        },
        clearErrInfo() {
            this.errInfo = ''
        }
    },
    watch: {
        email: 'clearErrInfo',
        password: 'clearErrInfo'
    }
}
</script>

<style scoped>
.bg-gray {
    padding: 1rem;
    background: #f8f9fa;
    color: #667189;
}

.bg-header {
    background: #ffffff;
    box-shadow: 0 0 12px 0 rgba(0, 0, 0, .1);
}

.section-header {
    position: fixed;
    top: 0;
    z-index: 200;
    padding: 1rem .5rem;
    width: 100%;
}

.grid-header .navbar {
    height: 4rem;
}

.grid-header .navbar-section {
    padding: 0;
}

.grid-header .navbar-brand {
    color: #50596c;
}

.grid-header .btn-link {
    padding-right: .6rem;
    padding-left: .6rem;
    color: #667189;
}

.grid-header .btn-link:focus,
.grid-header .btn-link:hover,
.grid-header .btn-link:active {
    color: #50596c;
    opacity: .75;
}

.grid-footer {
    margin-top: 2rem;
    margin-bottom: 1rem;
    color: #acb3c2;
}

.grid-footer a {
    color: #727e96;
}


/*fix for header*/

.grid-header .btn-link {
    padding-right: 2rem;
    padding-left: 2rem;
}

.section-footer {
    position: fixed;
    bottom: 0px;
    left: 0px;
    width: 100%;
}

.section-login {
    margin-top: 180px;
}
</style>



